:root {
  /* Primary brand colors (extracted approximation from hero image) */
  /* Primary blue (white/blue/black theme) */
  --color-primary: #1a73ff; /* slightly lighter IBM-like blue */
  --color-primary-700: #0b59d6;
  --color-primary-rgb: 26,115,255;

  /* Neutrals */
  --color-text: #0b1721; /* near-black tuned */
  --color-muted: #5f6b74;
  --color-bg: #f7f8fa;
  --color-surface: #ffffff;

  /* Light accents */
  --color-light: #eef0ff;
  --color-muted-bg: #e9ecff;
  --color-alt-bg: #e2e6f3;

  --radius: 0.5rem;
}

html,body,#root {
  height: 100%;
}

body {
  margin: 0;
  font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  background: var(--color-surface);
  color: var(--color-text);
}
